import React from "react";
import { Spinner, Alert } from "reactstrap";
import "./spinner-comp.scss";
class SpinnerComp extends React.Component {
  // what to render when instantiate
  render() {
    const { runSpinner, onLoadMethod, label, isErrorOnLoad } = this.props;

    if (isErrorOnLoad) {
      return (
        <div>
          <Alert color="danger" className="text-center">
            Something Went Wrong While Fetching Database{" "}
            <strong>Retry Load More</strong> Button
          </Alert>
          <div className="container center-me-btn">
            <button onClick={onLoadMethod} className="btn btn-outline-primary">
              {label}
            </button>
          </div>
        </div>
      );
    }
    if (!runSpinner) {
      return (
        <div className="container center-me-btn">
          <button onClick={onLoadMethod} className="btn btn-outline-primary">
            {label}
          </button>
        </div>
      );
    } else {
      return (
        <div className="container center-me-btn">
          <Spinner color="primary" />
        </div>
      );
    }
  }
}
export default SpinnerComp;
