import React, { Component } from "react";
// import logo from './logo.svg';

import "./main.scss";

import NavComp from "./components/Navbar/nav-comp";
import HeroComp from "./components/Hero/hero-comp";
import ResultArea from "./components/Result/result-area";
import FooterComp from "./components/Footer/footer-comp";

class PublicPage extends Component {
  render() {
    return (
      <div>
        <NavComp isAuth={this.props.isAuth} />
        <HeroComp />
        <ResultArea />
        <FooterComp />
      </div>
    );
  }
}

export default PublicPage;
