import axios from "axios";

var url = "https://sai-fish-zone-api.herokuapp.com";

function getFromAPI(results = 6) {
  return axios.get(`${url}/api/fish?results=${results}`);
}

function authFromAPI(username, password) {
  return axios({
    method: "post",
    url: `${url}/api/ar-admin`,
    data: {
      username: username,
      password: password
    }
  });
}

function getTokenFormStorage() {
  //check for token existence if not found give error --> set unauthenticate
  return window.localStorage.getItem("token");
}

function createDocFromAPI({ fishName, fishTagList, isFishAvial, imageList }) {
  var token = getTokenFormStorage();
  // console.log(fishName, fishTagList, isFishAvial, imageList);
  return axios({
    method: "post",
    url: `${url}/api/fish`,
    data: {
      image: imageList,
      available: isFishAvial,
      name: fishName,
      fishType: fishTagList
    },
    headers: {
      Authorization: `Bearer ${token}`
    }
  });
}

function getOneDocFromAPI(id) {
  return axios({
    method: "get",
    url: `${url}/api/fish/${id}`
  });
}

function deleteDocFromAPI(id) {
  var token = getTokenFormStorage();
  return axios({
    method: "delete",
    url: `${url}/api/fish/${id}`,
    headers: { Authorization: `Bearer ${token}` }
  });
}

function updateOneDocFromAPI({
  id,
  imageList,
  isFishAvial,
  fishName,
  fishTagList
}) {
  var token = getTokenFormStorage();
  return axios({
    method: "put",
    data: {
      image: imageList,
      available: isFishAvial,
      name: fishName,
      fishType: fishTagList
    },
    url: `${url}/api/fish/${id}`,
    headers: { Authorization: `Bearer ${token}` }
  });
}
