function refreshAcsessToken() {
  return axios({
    method: "post",
    url: "https://api.imgur.com/oauth2/token",
    data: {
      client_id: "7dbe33876bc12ee",
      client_secret: "c937a428ae34c88d001c409fd01bd934c0802ded",
      refresh_token: "8e3cc9cee1be8920c035d18bce78db408a6a226c",
      grant_type: "refresh_token"
    },
    headers: {
      "Content-Type": "application/json"
    }
  });
}

function postImageOnImagur(slug, title) {
  var formData = new FormData();
  formData.append("image", slug);
  formData.append("title", title);
  formData.append("name", title);
  formData.append("description", "uploaded from sai-fish-zone-react-app");
  formData.append("type", "jpg");
  var access_token = window.localStorage.access_token;
  return axios({
    method: "post",
    url: "https://api.imgur.com/3/image",
    data: formData,
    headers: {
      Authorization: `Bearer ${access_token}`,
      "content-type": "multipart/form-data"
    }
  });
}

function shouldAccessTokenBeRefresh() {
  var currentTime = new Date();
  // currentTime=`${currentTime.getUTCDate()}-${currentTime.getUTCMonth()+1}-${currentTime.getUTCFullYear()}`;//8-3-2019
  var [cdate, cmonth, cyear] = [
    currentTime.getUTCDate(),
    currentTime.getUTCMonth() + 1,
    currentTime.getUTCFullYear()
  ];
  var reftknCall = window.localStorage.getItem("refTknCall"); // 2-2-2019
  reftknCall = reftknCall.split("-"); //[0]->d [1]->m [2]->y
  reftknCall = reftknCall.map(i => +i);
  // var calcD = cdate - reftknCall[0]; //should be
  // var calcM = cmonth - reftknCall[1]; //should be 0
  // var calcY = cyear - reftknCall[2]; //should be 0
  var calcD = cdate - reftknCall[0]; //should be
  var calcM = cmonth - reftknCall[1]; //should be 0
  var calcY = cyear - reftknCall[2]; //should be 0

  if (calcM || calcY !== 0) {
    //you should refresh token and store current date`
    return true;
  } else if (calcD > 10) {
    //you should refresh token and store current date
    return true;
  } else {
    // console.log("no need to refresh token");
    return false;
  }
}

function resettingTheAccessTokenFromRefreshToken() {
  var cd = new Date();
  cd = `${cd.getUTCDate()}-${cd.getUTCMonth() + 1}-${cd.getUTCFullYear()}`; //8-3-2019
  // console.log("should update token");
  var store = window.localStorage;
  refreshAcsessToken()
    .then(succ => {
      store.setItem("access_token", succ.data.access_token);
      store.setItem("refTkn", succ.data.refresh_token);
      store.setItem("refTknCall", cd);
    })
    .catch(err => {
      console.error(`IMGUR TOKEN RELATED ERROR : ${err}`);
    });
}

export {
  getFromAPI,
  authFromAPI,
  createDocFromAPI,
  refreshAcsessToken,
  postImageOnImagur,
  getOneDocFromAPI,
  deleteDocFromAPI,
  updateOneDocFromAPI,
  shouldAccessTokenBeRefresh,
  resettingTheAccessTokenFromRefreshToken
};
