import React from "react";
import "./hero-comp.scss";
class HeroComp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      brand: "Sai Fish Zone",
      features: ["Aquarium Service", "Decorative", "Food & Medic"],
      tagLine: "Get Adorable Aquatic Pets!",
      callToAction: {
        msg: "Query Your Aquarium Now",
        url: "tel:917017446061"
      }
    };
  }

  //what to render when instantiate
  render() {
    return (
      // background:url('./fish-gang.png') center center no-repeat;
      <section role="main">
        <div className="jumbotron bg-hero">
          <div className="container-fluid">
            <h1 className="jumbotron-heading h1 font-weight-bold text-primary">
              {this.state.brand}
            </h1>
            {this.state.features.map(feat => (
              <span
                key={feat}
                className="badge badge-pill badge-primary font-weight-bold h4 mx-1"
              >
                {feat}
              </span>
            ))}
            <p className="text-muted my-1">{this.state.tagLine}</p>
            <p>
              <a
                href={this.state.callToAction.url}
                className="btn btn-primary my-1"
              >
                <img
                  width="35"
                  height="35"
                  alt=""
                  src="data:image/png;base64,"
                />
                {" " + this.state.callToAction.msg}
              </a>
            </p>
          </div>
        </div>
      </section>
    );
  }
}

export default HeroComp;
