import React from "react";
import "./card-comp.scss";
import ModalComp from "../Modal/modal-comp";
import { Link } from "@reach/router";
//TODO: create result section

class CardComp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fishName: "Loading Fish",
      tags: ["loadTag#1", "loadTag#4"],
      isAvailable: true,
      timeStamp: "29th nov, 1867",
      photos: [
        {
          src: "https://via.placeholder.com/250/?text=111Loading"
        },
        {
          src: "https://via.placeholder.com/250/?text=222Loading-2"
        },
        {
          src: "https://via.placeholder.com/250/?text=233Loading-3"
        }
      ]
    };
  }

  //after initial render
  componentDidMount() {
    var { fishName, fishTags, fishImg, isAvailable, fishUpdateAt } = this.props;
    fishUpdateAt = fishUpdateAt
      .split("T")[0]
      .split("-")
      .reverse()
      .join(" ");
    fishTags = fishTags.split("_");
    fishImg = [...fishImg.map(i => ({ src: i }))];
    this.setState({
      fishName: fishName,
      tags: fishTags,
      isAvailable: isAvailable,
      photos: fishImg,
      timeStamp: fishUpdateAt
    });
    // window.addEventListener("storage", this.isUserAuthenticated);
  }

  //what to render when instantiate
  render() {
    const { id } = this.props;
    // isAvailable = Boolean(+isAvailable); //coercion to number then boolean
    return (
      <div className="col-md-4">
        <div className="card mb-4 box-shadow">
          <img
            className="card-img-top"
            src={this.state.photos[0].src.valueOf()}
            alt={this.state.fishName}
            height="250"
          />
          <div className="card-body">
            <div className="card-first-head text-capitalize">
              <strong>{this.state.fishName}</strong>
              <div>
                <span
                  className={
                    this.state.isAvailable
                      ? "badge badge-success"
                      : "badge badge-danger"
                  }
                >
                  {this.state.isAvailable ? "Available" : "Unavailable"}
                </span>
              </div>
            </div>
            <p className="card-text text-muted text-capitalize">
              Tags:{" "}
              {this.state.tags.map(tag => (
                <span
                  //we're using tag as its key coz no-two-tags are same
                  key={tag}
                  href="#"
                  className="badge badge-pill badge-dark mx-1"
                >
                  {tag}
                </span>
              ))}
            </p>

            <div className="d-flex justify-content-between align-items-center">
              <div className="btn-group">
                <a
                  href="tel:9557754678"
                  className="btn btn-sm btn-outline-primary mx-1"
                >
                  Call Now
                </a>

                <ModalComp
                  buttonLabel="Photos"
                  className="exampleModal"
                  photosLength={this.state.photos.length}
                  modalTitle={this.state.fishName}
                  carouselImg={this.state.photos}
                />
                {window.localStorage.token ? (
                  <Link
                    to={`/main-admin-zone/${id}`}
                    className="btn btn-sm btn-ouline-warning "
                  >
                    Edit
                  </Link>
                ) : null}
              </div>
              <small className="text-muted">{this.state.timeStamp}</small>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default CardComp;
