import React from "react";
import CardComp from "../Card/card-comp";
import "./result-area.scss";
import { getFromAPI } from "../../fish-client"; //it's index.js
import AlertComp from "../Alert/alert-comp";
import SpinnerComp from "../Spinner/spinner-comp";

//todo make separate file for loadmore
// import { Spinner } from "reactstrap";
class ResultArea extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fish: [],
      counter: 6,
      isError: false,
      isErrorOnLoad: false,
      spinnerRun: false
    };
    this.onLoadMoreClick = this.onLoadMoreClick.bind(this);
  }

  //integration with API Client =>> fish-client
  componentDidMount() {
    // var fish = [];
    getFromAPI()
      .then(dataObj => {
        this.setState({ fish: dataObj.data });
        // console.log(this.state);
      })
      .catch(err => this.setState({ isError: true }));
  }

  // event handler of load more
  onLoadMoreClick() {
    //callback function that will run just after the setState
    const getAPI = async () => {
      var counter = this.state.counter;
      await spinnerToggler();
      getFromAPI(counter)
        .then(dataObj => {
          this.setState({ isErrorOnLoad: false });
          this.setState({ fish: dataObj.data });
          // console.log(this.state);
        })
        .catch(err => this.setState({ isErrorOnLoad: true }));
      await spinnerToggler();
    };
    //spinner toggler
    const spinnerToggler = () => {
      var spinner = this.state.spinnerRun;
      // console.log("from spinner");
      this.setState({ spinnerRun: !spinner });
    };

    this.setState(
      (state, props) => ({
        counter: state.counter + 6
      }),
      getAPI
    );
  }

  //what to render when instantiate
  render() {
    return (
      <section className="album py-5 bg-doodle">
        <div className="container" id="main-results">
          <AlertComp isError={this.state.isError} />
          <div className="row">
            {this.state.fish.map(fish => (
              <CardComp
                fishName={fish.name}
                fishTags={fish.fishType}
                fishImg={fish.image}
                isAvailable={fish.available}
                fishUpdateAt={fish.updatedAt}
                id={fish._id}
                key={fish._id}
              />
            ))}
          </div>
          <SpinnerComp
            runSpinner={this.state.spinnerRun}
            label="Load More"
            onLoadMethod={this.onLoadMoreClick}
            isErrorOnLoad={this.state.isErrorOnLoad}
          />
        </div>
      </section>
    );
  }
}
export default ResultArea;
