import React from "react";
import "./login-comp.scss";
import { authFromAPI } from "../../fish-client";
// import { Alert } from "reactstrap";
import RedirectTo from "../CURD/redirect";
import { Link } from "@reach/router";
// import { Redirect } from "@reach/router";
class LoginComp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: "",
      data: "",
      isAuthenticated: false,
      isAuth: false
    };

    //binding ASYNC event handler to LoginComp's Object Context
    this.handlePasswordOnChange = this.handlePasswordOnChange.bind(this);
    this.handleUsernameOnChange = this.handleUsernameOnChange.bind(this);
    this.handleFormOnSubmit = this.handleFormOnSubmit.bind(this);
  }

  componentDidMount() {
    var token = window.localStorage.getItem("token");
    if (token) {
      this.setState({ isAuth: true });
    }
  }
  //event handler for username onChange
  handleUsernameOnChange(event) {
    this.setState({
      username: event.target.value
    });
  }

  //event handler for password onChange
  handlePasswordOnChange(event) {
    this.setState({
      password: event.target.value
    });
  }

  //event handler for onSubmit form
  handleFormOnSubmit(event) {
    //drawback of using PROMISE, on retry correct password again will produces same result
    authFromAPI(this.state.username, this.state.password)
      .then(succ => {
        if (succ.data.token) {
          window.localStorage.setItem("token", succ.data.token);
          this.setState({ isAuthenticated: true });
        }
      })
      .catch(err => this.setState({ isError: true }));

    //Stopping default behaviour
    event.preventDefault();
  }

  //what to render when instantiated
  render() {
    return (
      <div className="text-center login ">
        <form className="form-signin" onSubmit={this.handleFormOnSubmit}>
          <div className="login-img rounded mx-auto" />
          <h1 className="h3  font-weight-normal">Login</h1>
          {!this.state.isAuth ? (
            <React.Fragment>
              <input
                type="text"
                id="inputUsername"
                className="form-control"
                placeholder="Username"
                required
                autoFocus
                value={this.state.username}
                onChange={this.handleUsernameOnChange}
              />

              <input
                type="password"
                id="inputPassword"
                className="form-control"
                placeholder="Password"
                value={this.state.password}
                onChange={this.handlePasswordOnChange}
                required
              />

              <button
                className="btn btn-lg btn-primary btn-block mt-3"
                type="sumbit"
              >
                LogIn
              </button>
            </React.Fragment>
          ) : (
            <Link to="/main-admin-zone/create-only">
              <div className="btn btn-primary">Create Fish</div>
            </Link>
          )}

          <p className="mt-3 text-muted">
            S A I - F I S H - Z O N E &copy; 2 0 1 8 - 2 0 1 9
          </p>
        </form>
        <RedirectTo isAuthenticated={this.state.isAuthenticated} />
      </div>
    );
  }
}

export default LoginComp;
