import React from "react";
import "./nav-comp.scss";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from "reactstrap";
import { Link } from "@reach/router";

export default class NavComp extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  handleLogOutOnClick = event => {
    window.localStorage.removeItem("token");
    event.preventDefault();
    event.stopPropagation();
  };
  render() {
    return (
      <div>
        <Navbar expand="md" className="">
          <NavbarBrand href="/" className="font-weight-bold h3 my-0">
            <img
              src="data:image/png;base64,"
              width="45"
              height="47"
              alt="main logo"
              className="rounded"
            />
          </NavbarBrand>
          <NavbarToggler onClick={this.toggle}>
            <img
              src="data:image/png;base64,"
              alt=""
              width="24"
              height="24"
            />
          </NavbarToggler>
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto btn-group" navbar>
              <NavItem>
                <NavLink
                  href="#main-results"
                  className="text-capitalize text-bold btn btn-primary mx-1"
                >
                  <img
                    alt=""
                    width="20"
                    height="20"
                    src="data:image/png;base64,"
                  />
                  &nbsp;Gallery
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  href="#contact-us"
                  className="text-capitalize text-bold btn btn-primary mx-1"
                >
                  <img
                    alt=""
                    width="20"
                    height="20"
                    src="data:image/png;base64,"
                  />
                  &nbsp;Contact
                </NavLink>
              </NavItem>
              <NavItem>
                <Link to="/ar-admin">
                  <div className="btn btn-primary h-100 w-100 rounded mx-1 text-center">
                    <img
                      alt=""
                      width="20"
                      height="20"
                      src="data:image/png;base64,"
                    />
                    &nbsp;Login
                  </div>
                </Link>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}
