import React from "react";
import { Alert } from "reactstrap";
class AlertComp extends React.Component {
  //render this when instantiate
  render() {
    const { isError } = this.props;
    if (isError) {
      return (
        <div>
          <Alert color="danger">
            <h4 className="alert-heading">OOPS! Something Went Wrong!!!</h4>
            <p>
              It Seems that We Have Encountered a Problem While Processing your
              Request
            </p>
            <strong>Check few Things</strong>
            <ul>
              <li>Internet Connection</li>
              <li>
                Try to Restart This Application Using Pressing <code>F5</code>{" "}
                or Revisting this Url
              </li>
            </ul>

            <hr />
            <p className="mb-0">We are Extreamly Sorry About Inconvenience</p>
          </Alert>
        </div>
      );
    } else {
      return <div />;
    }
  }
}
export default AlertComp;
